"use client";

import { Bold, Italic, List, ListOrdered } from "lucide-react";
import React, { useRef } from "react";
import { ToggleGroup, ToggleGroupItem } from "./ui/toggle-group";
import { cn } from "@/lib/utils";
import { Separator } from "./ui/separator";

interface RichTextEditorProps {
    value: string;
    onChange: (value: string) => void;
    placeholder?: string;
}

export function RichTextEditor({ value, onChange, placeholder }: RichTextEditorProps) {
    const editorRef = useRef<HTMLDivElement>(null);

    const handleInput = (event: React.FormEvent<HTMLDivElement>) => {
        onChange(event.currentTarget.innerHTML);
    };

    const execCommand = (command: string, value?: string) => {
        document.execCommand(command, false, value);
        if (editorRef.current) {
            onChange(editorRef.current.innerHTML); // Update state after command
            editorRef.current.focus();
        }
    };
    
    return (
        <div className="rounded-md border border-input bg-background focus-within:ring-2 focus-within:ring-ring focus-within:ring-offset-2">
            <div className="p-2 border-b border-input">
                <ToggleGroup type="multiple" className="flex items-center justify-start">
                    <ToggleGroupItem value="bold" aria-label="Toggle bold" onClick={() => execCommand('bold')}>
                        <Bold className="h-4 w-4" />
                    </ToggleGroupItem>
                    <ToggleGroupItem value="italic" aria-label="Toggle italic" onClick={() => execCommand('italic')}>
                        <Italic className="h-4 w-4" />
                    </ToggleGroupItem>
                    <Separator orientation="vertical" className="h-6 mx-1" />
                    <ToggleGroupItem value="bullet" aria-label="Toggle bullet list" onClick={() => execCommand('insertUnorderedList')}>
                        <List className="h-4 w-4" />
                    </ToggleGroupItem>
                    <ToggleGroupItem value="ordered" aria-label="Toggle ordered list" onClick={() => execCommand('insertOrderedList')}>
                        <ListOrdered className="h-4 w-4" />
                    </ToggleGroupItem>
                </ToggleGroup>
            </div>
            <div
                ref={editorRef}
                contentEditable={true}
                onInput={handleInput}
                dangerouslySetInnerHTML={{ __html: value }}
                className={cn(
                    "min-h-[200px] w-full rounded-b-md bg-transparent px-3 py-2 text-sm shadow-sm placeholder:text-muted-foreground focus-visible:outline-none",
                    !value && "text-muted-foreground"
                )}
                 // The 'before' pseudo-element for placeholder text.
                data-placeholder={placeholder}
            />
             <style jsx>{`
                [contentEditable=true][data-placeholder]:empty:before {
                    content: attr(data-placeholder);
                    color: hsl(var(--muted-foreground));
                    pointer-events: none;
                    display: block; /* For certain browsers */
                }
            `}</style>
        </div>
    );
}
